import * as React from "react";
import Navigation, { NavigationItem } from "src/components/Navigation/Navigation";
import Banner from "src/components/Banner";
import styled from "styled-components";

export interface SullivanProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
        }
        & > div:nth-child(2) {
            width: 80%;
            line-height: 1.71;
            font-size: 14px;

            & > p:nth-child(1) {
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;

                &.title {
                    font-size: 32px;
                    font-weight: bold;

                    margin-bottom: 34px;
                }

                &.subTitle {
                    font-size: 22px;
                    font-weight: bold;

                    margin-bottom: 10px;
                }
            }
        }
    }
`;

export default class Sullivan extends React.Component<SullivanProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "브랜드", link: "/brand/" }, { name: "카페 설리번", link: "/sulivan" }]}
                >
                    <NavigationItem link="http://www.sullivan.co.kr">Sullivan 홈페이지 바로가기</NavigationItem>
                </Navigation>
                <Content>
                    <Banner>
                        <img src={require("../../assets/brand/sullivan/sullivan-banner.png")} alt="" />
                        <div className="desc typing">
                            <h1 lang="en" style={{ color: "white" }}>
                                Sullivan
                            </h1>
                            <h2 style={{ color: "white" }}>바쁜 일상에 속에서 한 줌의 여유를 느끼다</h2>
                        </div>
                    </Banner>
                    <Desc>
                        <div>
                            <div />
                            <div>
                                <p className="title">여유를 찾고 싶은 오늘 카페 설리번으로</p>
                            </div>
                        </div>
                        <div>
                            <div>
                                <img src={require("../../assets/brand/sullivan/sullivan-logo.png")} style={{ width: "100%" }} alt="" />
                            </div>
                            <div>
                                <p className="subTitle">#헬렌 켈러와 카페 설리번</p>
                                <p>
                                    ‘헬렌 켈러의 스승 설리번?’ 카페의 이름을 얘기했을 때, 몇 번이고 익숙한 물음이 날아왔다. <br />
                                    초등학생 때 들었던 오래된 단어가 기억 속에서 툭 튀어나와 낯선 대답을 무마시켰다.
                                    <br />
                                    <br />
                                    시각, 청각을 모두 잃은 헬렌 켈러에게 손을 내민 한 사람. <br />
                                    설리번은 헬렌 켈러에게 전에 없던 감각과 온기를 선물했다. <br />
                                    <br />
                                    카페는 말 그대로 스승 ‘앤 설리번’에서 이름을 따왔다. <br />
                                    지친 일상에 내면을 닫고 사는 사람들, 시간에 쫓겨 사느라 오감이 무뎌진 현대인들에게 카페는 설리번의
                                    따뜻한 마음을 전하려는 것이다.
                                </p>
                            </div>
                        </div>

                        <div>
                            <div />
                            <div>
                                <p className="subTitle">#카페의 온도</p>
                                <p>
                                    카페에 도착했을 때 나지막이 Chet Baker의 연주가 흘러나왔다. <br />
                                    ♬I'm glad there’s you (네가 있어 기쁘다) <br />
                                    이 부분 읽을 때 ♬I'm glad there’s you 노래가 흘러나오게 재생 버튼을 넣을 수 있을까요?
                                    <br />
                                    어서오세요- 형식적인 여느 인사보다 음악으로 건네는 따뜻한 말 한마디가 청각을 먼저 반응하게 만들었다.{" "}
                                    <br />
                                    <br />
                                    그 다음은 시각과 후각. <br />
                                    <br />
                                    &lt;헬렌 켈러 中&gt;
                                    <br />
                                    집 앞엔 숲이 있었다. 그 곳을 산책한 친구는 ‘별거’ 없다고 말했다.
                                    <br />
                                    ‘어떻게 숲에서 아무것도 느낄 수 없단 걸까?’
                                    <br />
                                    그녀는 앞을 볼 수 없었지만, 손끝의 감각만으로 숲의 구석구석을 볼 수 있었다. <br />
                                    <br />
                                    카페 전체를 감싸고 있는 나무는 공간의 온기를 더하기에 충분했다. <br />
                                    커피로 한 번, 두 번 덧칠을 해 색을 입힌 나무에선 은은한 커피향이 감돌았다. <br />
                                    헬렌 켈러가 손끝으로 그려낸 숲도 이런 편안함과 아늑함을 가져다 줬을까. <br />
                                    따뜻한 라떼를 시키고 푹신한 패브릭 소파에 푹 들어앉으니 전에 없던 오후를 선물 받은 느낌이었다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p>
                                    <img
                                        src={require("../../assets/brand/sullivan/img1.png")}
                                        style={{ width: "90%", height: 458, backgroundColor: "#e9e9e9" }}
                                        alt=""
                                    />
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">#기분좋은 여운</p>
                                <p>
                                    넓은 창 안으로 쏟아지는 햇살이 눈으로 바뀌고, 비로 바뀌어도 참 운치 있겠다는 생각을 한 찰나, <br />
                                    공간 한쪽 끝에서 베이커리 향이 진하게 배어나왔다. <br />
                                    ‘파티쉐가 직접 굽는 데일리 베이커리’란 콘셉트의 카페답게 누군가에게는 ‘빵집’으로 더 유명한 곳이다.
                                    <br />
                                    <br />
                                    연유파이, 말차 초코스콘, 레몬 마들렌, 크로와상, 프레첼... 이름만 들어도 미각을 자극하는 빵들이 트레이에
                                    놓이고, <br />
                                    사람들은 분주하게 혀끝에 닿을 ‘맛’을 담기 시작했다.
                                    <br />
                                    <br />
                                    그 순간의 설렘과 호기심은 커피와 어울려 더욱 진하게 공간 안에 남았다. 그래서일까. <br />
                                    ‘머무는 것만으로도 여행이 될 수 있겠구나’ 생각이 드는 카페는 이곳이 처음이었다. 잠시나마 내 감각들을
                                    깨울 수 있는 곳, <br />
                                    작은 위로를 받을 수 있는 곳이란 생각에 다음 날도, 그 다음 날도 카페의 시간을 오롯이 몸으로 기억해낼 수
                                    있었다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">#여유를 채우는 공간, 오감을 깨우는 카페</p>
                                <p>
                                    “테이크아웃이요” 늘 바쁘게 들고 나오던 커피는 왠지 몰라도 빨리 식었다. <br />
                                    하지만 카페 설리번의 온기는 그 시간을 더욱 길게 만드는 묘한 매력이 있다. <br />
                                    오감을 자극하기 위해 음악 한 곡, 커피와 베이커리가 풍기는 향 하나까지 큐레이팅하는 카페 설리번. <br />
                                    누구라도 이곳에 오면 이 기분 좋은 경험을 많은 이들과 함께 나누고 싶은 생각이 들 것이다.
                                    <br /> <br /> <br />
                                    ⚀ 카페 설리번 서초점 (서울 서초구 효령로 355) <br />
                                    평일 08:00 – 22:00 주말 10:00 – 21:00 <br />
                                    ☏02-598-0355 <br />
                                    <br />
                                    ⚁ 카페 설리번 교대점 (서울 서초구 서초대로56길 18) <br />
                                    평일 08:00 – 22:00 주말 08:00 – 22:00 <br />
                                    ☏02-3474-7090 <br />
                                    <br />
                                    ⚂ 카페 설리번 당산점 (서울 영등포구 당산로 241) <br />
                                    평일 07:00 – 23:00 주말 08:00 – 23:00 <br />
                                    ☏02-2068-8825
                                </p>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



